.breadcrumb li {
    vertical-align: middle;
}

.breadcrumb li:first-child {
    line-height: 1;
    a {
        font-size: 1.3em;
        text-align: center;
        padding-right: 0.85em;
    }
}

.breadcrumb,
.breadcrumb>.active {
    color: #000000!important;
}

.breadcrumb>.active {
    font-weight: bold;
}

.breadcrumb>li+li:before {
    background-image: url("data:image/png;base64,");
    background-position: center center;
    background-repeat: no-repeat;
    content: "";
    display: inline-block;
    height: 48px;
    vertical-align: middle;
}

.breadcrumb li>a,
.breadcrumb li>span {
    color: inherit;
    font-size: .875em;
    line-height: 1rem;
    padding: 1rem .85em;
    padding-right: 1.75em;
    position: relative;
    text-align: center;
    min-height: 0;
}

.breadcrumb li>a {
    transition: color 0.1s;
    &:hover,
    &:focus,
    &:active {
        background-color: transparent !important;
        color: #666 !important;
    }
}